//
var Wi = [7, 9, 10, 5, 8, 4, 2, 1, 6, 3, 7, 9, 10, 5, 8, 4, 2, 1];    // 
var ValideCode = [1, 0, 10, 9, 8, 7, 6, 5, 4, 3, 2];            // .10X
function checkIdCard(idCard, flag) {
    idCard = idCard || '';
    flag = flag || 1;
    if (flag == 1 && idCard.indexOf("****") != -1) {
        return true;
    }
    idCard = trim(idCard.replace(/ /g, ""));               //
    if (idCard.length == 15) {
        return isValidityBrithBy15IdCard(idCard);       //15
    } else if (idCard.length == 18) {
        var a_idCard = idCard.split("");                // 
        if (isValidityBrithBy18IdCard(idCard) && isTrueValidateCodeBy18IdCard(a_idCard)) {   //1818
            return true;
        } else {
            return false;
        }
    } else {
        return false;
    }
}
/**
 * 18
 * @param a_idCard 
 * @return
 */
function isTrueValidateCodeBy18IdCard(a_idCard) {
    var sum = 0;                             // 
    if (a_idCard[17].toLowerCase() == 'x') {
        a_idCard[17] = 10;                    // x10
    }
    for (var i = 0; i < 17; i++) {
        sum += Wi[i] * a_idCard[i];            // 
    }
    var valCodePosition = sum % 11;                // 
    if (a_idCard[17] == ValideCode[valCodePosition]) {
        return true;
    } else {
        return false;
    }
}
/**
 * 18
 * @param idCard 18
 * @return
 */
function isValidityBrithBy18IdCard(idCard18) {
    var year = idCard18.substring(6, 10);
    var month = idCard18.substring(10, 12);
    var day = idCard18.substring(12, 14);
    var temp_date = new Date(year, parseFloat(month) - 1, parseFloat(day));
    // getFullYear()
    if (temp_date.getFullYear() != parseFloat(year)
        || temp_date.getMonth() != parseFloat(month) - 1
        || temp_date.getDate() != parseFloat(day)) {
        return false;
    } else {
        return true;
    }
}
//
function trim(str) {
    return str.replace(/(^\s*)|(\s*$)/g, "");
}

//*
function plusXing(str, frontLen, endLen) {
    var len = str.length - frontLen - endLen;
    var xing = '';
    for (var i = 0; i < len; i++) {
        xing += '*';
    }
    return str.substring(0, frontLen) + xing + str.substring(str.length - endLen);
}
//idjson
function uniqueArray(array, key) {
    var result = [array[0]];
    for (var i = 1; i < array.length; i++) {
        var item = array[i];
        var repeat = false;
        for (var j = 0; j < result.length; j++) {
            if (item[key] == result[j][key]) {
                repeat = true;
                break;
            }
        }
        if (!repeat) {
            result.push(item);
        }
    }
    return result;
}

export default {
    getUrlKey,
    formatPicture,
    getTimeFormatText,
    setTitle,
    jumpLogin,
    checkIdCard,
    plusXing,
    onClickLeft,
    formatDistance,
    timeOVer,
    timeStampOVer,
    uniqueArray
}