<template>
    <f7-page>

        <f7-block>
            <span class="c-666"></span>
        </f7-block>

        <f7-block class="mt-20">
            <van-button type="info" @click="joinWelfare"></van-button>
        </f7-block>
    </f7-page>
</template>

<script>
    import {certificate1,getMineInfo,welfareUser} from "@/api/api_community.js";
    export default {
        data(){
            return {
                sysOrgCode:''
            };
        },
        created(){
            this.$commonUtils.setTitle('');
            this.sysOrgCode = this.$commonUtils.getUrlKey("code");
        },
        methods: {
            joinWelfare(){
                this.$dialog.confirm({message:''}).then(() =>{
                    wx.loading('...');
                    welfareUser({sysOrgCode:this.sysOrgCode}).then(res => {
                        wx.closeLoading();
                        wx.toast('');
                        setTimeout(function(){
                            wx.popPage(1);
                        },1500);
                    }).catch(err => {
                        wx.closeLoading();
                    });
                })
            }
        }
    }
</script>

